nav
{
    background: rgba(0,0,0,0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem; 
    display: flex;
    /* align-items: center; */
    gap: 0.7rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a
{
    background: transparent;
    padding: 0.5rem 0.9rem;
    border-radius: 50%;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover
{
    background: rgba(0,0,0,0.3);
}

nav a.active
{
    background-color: var(--color-primary);
    color: var(--color-bg);
}















